





























import clone from "@/lib/clone";
import vue from "vue";
import { Component, Prop, Watch } from "vue-property-decorator";

@Component({})
export default class Tags extends vue {
  @Prop(String) Type = "-";
  // tagList = store.fetchTags();
  @Watch("Type")
  onTypechange(newType: string) {
    this.Type = newType;
    // console.log(this.Type);
  }

  get tagList() {
    return this.$store.state.tagList;
  }
  get TagList() {
    //这是按照类型过滤后的数据
    return this.findType();
  }
  beforecreated() {
    this.$store.commit("fetchTags");
  }
  selectTags: string[] = [];
  selected(tag: any) {
    const index = this.selectTags.indexOf(tag);
    if (index >= 0) {
      this.selectTags.splice(index, 1);
    } else {
      if (this.selectTags.length < 3) {
        this.selectTags.forEach((item: any) => {
          if (item.type !== this.Type) {
            this.selectTags = [];
          }
        });
        this.selectTags.push(tag);
      } else {
        this.selectTags = [];
        alert("最多选择3个标签！");
      }
    }
    this.$emit("update:value", this.selectTags);
  }
  addTags() {
    const Tagname = window.prompt("请输入新的标签名！");
    const tagType = this.Type;
    let flag = false;
    this.$store.commit("createTag", {
      name: Tagname,
      type: tagType,
      flag: flag,
    });
  }
  findType() {
    const TypeData = clone(this.tagList).filter(
      (item: any) => item.type === this.Type
    );
    return TypeData;
  }
}
